<template>
  <div id="my-home">
    <span>{{name}}</span>
    <span>{{count}}</span>
  </div>
</template>

<script>
  import {mapState} from 'vuex'

  export default {
    name: 'MyHome',
    data() {
      return {
        count: 1
      }
    },
    computed: {
      countAdd() {
        return this.count + 1
      },
      ...mapState({
      name: state => state.user.name
      })
    }
  }
</script>

